<template>
  <div>
    <div class="p-3">
      <h1>Thêm sản phẩm</h1>
    </div>

    <form
      class="row g-3 needs-validation"
      @submit.prevent="formSubmit()"
      novalidate
    >
      <!-- Thông tin cơ bản -->
      <div class="border shadow p-3">
        <h4 class="text-center p-2">Thông tin cơ bản</h4>

        <div class="mb-3 row col-md-8">
          <label class="col-sm-4 col-form-label">Tên sản phẩm:</label>
          <div class="col-sm-8">
            <input
              class="form-control"
              type="text"
              placeholder="Nhập vào tên sản phẩm"
              aria-label="default input example"
              v-model="productName"
              required
            />
            <div class="invalid-feedback">Tên sản phẩm không được để trống</div>
          </div>
        </div>

        <div class="mb-3 row col-md-8">
          <label class="col-sm-4 col-form-label">Danh mục sản phẩm:</label>
          <div class="col">
            <div class="position-relative">
              <input
                id="category-id"
                class="form-control bg-light-subtle"
                type="text"
                placeholder="Chọn danh mục sản phẩm"
                required
              />
              <div class="invalid-feedback">Chọn danh mục sản phẩm</div>
              <div
                class="form-control position-absolute top-0 bottom-0 bg-primary opacity-0"
                aria-label="default input example"
                data-bs-toggle="modal"
                data-bs-target="#exampleModal"
                style="cursor: pointer"
              ></div>
            </div>

            <!-- Modal -->
            <div
              class="modal fade"
              id="exampleModal"
              tabindex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
            >
              <div class="modal-dialog modal-dialog-scrollable">
                <div class="modal-content">
                  <div class="modal-header">
                    <h1 class="modal-title fs-5" id="exampleModalLabel">
                      Danh mục sản phẩm
                    </h1>
                    <button
                      type="button"
                      class="btn-close"
                      data-bs-dismiss="modal"
                      aria-label="Close"
                    ></button>
                  </div>
                  <div class="modal-body">
                    <ul
                      class="list-group list-group-flush"
                      v-for="category in categorys"
                      :key="category.id"
                    >
                      <div class="list-group-item border">
                        <div
                          class="d-flex justify-content-between align-items-center"
                          style="height: 50px"
                        >
                          <div class="row col-10">
                            <div class="col-sm-11">
                              {{ category.categoryName }}
                            </div>
                            <div class="col-sm-1">
                              <input
                                type="radio"
                                class="form-check-input border border-secondary"
                                id="validationFormCheck2"
                                v-model="categoryChoseTemp"
                                :value="category.id"
                                name="radio-stacked"
                              />
                            </div>
                          </div>
                          <button
                            class="list-group-item accordion-button btn-show-list"
                            type="button"
                            data-bs-toggle="collapse"
                            :data-bs-target="`#collapseTwo${category.id}`"
                            aria-expanded="false"
                            v-if="category.categoryChild.length != 0"
                            @click="toggleElement(category.id)"
                          >
                            <font-awesome-icon
                              :icon="['fas', 'angle-down']"
                              v-if="elements[category.id] == false"
                            />
                            <font-awesome-icon
                              :icon="['fas', 'angle-up']"
                              v-if="elements[category.id] == true"
                            />
                          </button>
                        </div>
                        <div
                          :id="`collapseTwo` + category.id"
                          class="accordion-collapse collapse"
                        >
                          <div
                            v-for="child in category.categoryChild"
                            :key="child"
                          >
                            <div class="d-flex justify-content-end">
                              <div class="col-md-11">
                                <RecursiveCategory
                                  :category="child"
                                  :baseUrl="baseUrl"
                                  @categoryChose="changeCategoryEvent"
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </ul>
                  </div>
                  <div class="modal-footer">
                    <button
                      type="button"
                      class="btn btn-secondary"
                      data-bs-dismiss="modal"
                    >
                      Huỷ
                    </button>
                    <button
                      type="button"
                      data-bs-dismiss="modal"
                      @click="btnChangeCategory()"
                      class="btn btn-primary"
                    >
                      Chọn
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="mb-3 row col-md-8">
          <label class="col-sm-4 col-form-label">Mô tả sản phẩm:</label>
          <div class="col">
            <textarea
              class="form-control"
              id="validationTextarea"
              placeholder="Nhập vào mô tả sản phẩm"
              v-model="description"
              required
            >
            </textarea>
            <div class="invalid-feedback">
              Mô tả sản phẩm không được để trống
            </div>
          </div>
        </div>
      </div>

      <!-- Thông tin bán hàng -->
      <div class="border shadow p-3">
        <h4 class="text-center p-2">Thông tin bán hàng</h4>

        <!-- Phân loại sản phẩm -->
        <div class="mb-3 row p-3">
          <div class="form-check col-md-4">
            <input
              type="radio"
              class="form-check-input"
              id="validationFormCheck2"
              name="radio-stacked2"
              v-model="isClassify"
              value="false"
            />
            <label class="form-check-label" for="validationFormCheck2">
              Sản phẩm 1 loại
            </label>
          </div>
          <div class="form-check col-md-4">
            <input
              type="radio"
              class="form-check-input"
              id="validationFormCheck3"
              name="radio-stacked2"
              v-model="isClassify"
              value="true"
            />
            <label class="form-check-label" for="validationFormCheck3">
              Phân loại sản phẩm
            </label>
          </div>
        </div>

        <div class="mb-3 row" v-if="isClassify == 'false'">
          <div class="mb-3 row col-md-8">
            <label class="col-sm-4 col-form-label">Hình ảnh sản phẩm:</label>
            <div class="col-sm-8">
              <input
                class="form-control"
                type="file"
                id="formFile"
                multiple
                required
                @change="inputImageProduct"
              />
              <div class="invalid-feedback">
                Hình ảnh bị thiếu, hãy chắc chắn sản phẩm có ít nhất một hình
                đại diện
              </div>
            </div>
          </div>
          <div class="row col-md-6">
            <label class="form-label"> Giá tiền(đồng) </label>
            <div class="col">
              <input
                class="form-control"
                type="number"
                min="1000"
                placeholder="Nhập vào giá sản phẩm"
                aria-label="default input example"
                required
              />
              <div class="invalid-feedback">Giá sản phẩm tối thiểu 1000đ</div>
            </div>
          </div>
          <div class="row col-md-6">
            <label class="form-label"> Số lượng trong kho </label>
            <div class="col">
              <input
                class="form-control"
                type="number"
                min="0"
                placeholder="Nhập vào giá sản phẩm"
                aria-label="default input example"
                value="0"
                required
              />
              <div class="invalid-feedback">
                Số lượng trong kho không được để trống
              </div>
            </div>
          </div>
        </div>

        <div class="mb-3" v-if="isClassify == 'true'">
          <!-- Thêm thuộc tính -->
          <div class="border rounded mb-3 p-3">
            <div
              class="border mb-3 p-3"
              v-for="(attribute, key) in attributes"
              :key="key"
            >
              <div class="mb-3 row">
                <label class="col-sm-2 col-form-label">
                  - Nhóm phân loại {{ key + 1 }}:
                </label>
                <div class="col-sm-5 col-form-label">
                  <input
                    class="form-control"
                    v-model="attribute.name"
                    required
                  />
                  <div class="invalid-feedback">
                    Hãy đặt tên cho nhóm phân loại
                  </div>
                </div>
                <div class="col-sm-1 col-form-label text-end">
                  <div
                    class="btn btn-outline-danger"
                    type="button"
                    @click="removeAttribute(key)"
                  >
                    Xóa
                  </div>
                </div>

                <div class="mb-3 p-3">
                  <div
                    class="mb-3 row p-3"
                    v-for="(value, i) in attribute.attributeValues"
                    :key="i"
                  >
                    <label class="col-sm-2 col-form-label">
                      + Phân loại: {{ i + 1 }}
                    </label>
                    <div class="col-sm-4 col-form-label">
                      <input
                        class="form-control"
                        v-model="value.value"
                        required
                      />
                      <div class="invalid-feedback">
                        Hãy đặt tên cho phân loại
                      </div>
                    </div>
                    <div class="col-sm-1 col-form-label text-end">
                      <div
                        class="btn btn-outline-danger"
                        type="button"
                        @click="removeAttributeValue(key, i)"
                      >
                        Xóa
                      </div>
                    </div>
                  </div>
                  <div class="col-sm-7 text-center">
                    <button
                      type="button"
                      class="btn m-3 btn-primary"
                      @click="addAttributeValue(key)"
                    >
                      Thêm phân loại nhóm {{ key + 1 }} +
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <!-- Button -->
            <button
              type="button"
              class="btn m-3 btn-primary"
              @click="addAttribute()"
            >
              Thêm nhóm phân loại +
            </button>
          </div>

          <!-- Bảng thuộc tính -->
          <div class="table-responsive">
            <table class="table table-striped table-hover table-bordered">
              <thead class="table-secondary">
                <tr>
                  <th
                    v-for="(attribute, key) in attributes"
                    :key="key"
                    scope="col"
                  >
                    {{ attribute.name }}
                  </th>
                  <th scope="col">Giá</th>
                  <th scope="col">Kho hàng</th>
                  <th scope="col">Hình ảnh</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="index in tableLength[0]" :key="index">
                  <RecursiveTable
                    :attributes="attributes"
                    :recursive="0"
                    :tableLength="tableLength"
                    :index="index"
                  />
                  <td>
                    <input
                      class="form-control"
                      type="text"
                      placeholder="Nhập giá"
                      aria-label="default input example"
                      required
                    />
                  </td>
                  <td>
                    <input
                      class="form-control"
                      type="number"
                      min="0"
                      placeholder="Nhập số lượng trong kho"
                      aria-label="default input example"
                      required
                    />
                  </td>
                  <td>
                    <input
                      class="form-control"
                      type="file"
                      id="formFile"
                      multiple
                      required
                      @change="inputImageProduct"
                    />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <!-- Button -->
      <div class="col-12 d-flex justify-content-end">
        <div class="p-3">
          <div
            class="btn btn-danger"
            @click="$router.push('/seller/product/list')"
          >
            Hủy
          </div>
        </div>
        <div class="p-3">
          <button class="btn btn-primary" type="submit">Đăng sản phẩm</button>
        </div>
      </div>
    </form>

    <!-- <video src="https://66ef-1-55-243-245.ngrok-free.app/product/file/07f3e55e40a342baa8febfcaaa3bbd91.mp4" width="400" controls autoplay>
      Your browser does not support HTML video.
    </video> -->
    <img
      :src="baseUrl + '/auth/file/607a525b8ee8417da50cde7968617ce9.jpg'"
      alt=""
    />
  </div>
</template>
<script>
import RecursiveTable from "./RecursiveTable.vue";
import RecursiveCategory from "./RecursiveCategory.vue";

import axios from "axios";
export default {
  components: {
    RecursiveTable,
    RecursiveCategory,
  },

  data() {
    return {
      categorys: [],
      productName: "",
      categoryChose: {},
      categoryChoseTemp: {},
      elements: {},
      isClassify: "false",
      isCheckedModal: true,
      imgage: [],
      price: {},
      description: "",
      attributes: [
        {
          name: "",
          attributeValues: [
            {
              value: "",
            },
          ],
        },
      ],
    };
  },

  props: ["baseUrl"],

  methods: {
    formSubmit() {
      const forms = document.querySelectorAll(".needs-validation");
      Array.from(forms).forEach((form) => {
        if (!form.checkValidity()) {
          form.classList.add("was-validated");
        } else {
          alert("Đăng sản phẩm thành công");
          this.addProduct();
        }
      });
    },

    inputImageProduct(event) {
      this.imgage.push(event.target.files[0]);
    },

    addAttribute() {
      this.attributes.push({
        name: "",
        attributeValues: [
          {
            value: "",
          },
        ],
      });
    },

    removeAttribute(index) {
      this.attributes.splice(index, 1);
    },

    addAttributeValue(index) {
      this.attributes[index].attributeValues.push({
        value: "",
      });
    },

    removeAttributeValue(key, index) {
      this.attributes[key].attributeValues.splice(index, 1);
    },

    async getListCategory() {
      try {
        const response = await axios.get(
          this.baseUrl + "/product/category?parent=1"
        );
        this.categorys = await response.data.content;
        this.elements = Array(this.categorys.length).fill(false);
        return response.data.content;
      } catch (error) {
        console.error(error);
      }
    },

    async getCategoryById(id) {
      try {
        const response = await axios.get(
          this.baseUrl + "/product/category/" + id
        );
        return response.data;
      } catch (error) {
        console.error(error);
      }
    },

    toggleElement(index) {
      this.elements[index] = !this.elements[index];
    },

    async addProduct() {
      try {
        const response = await axios.post(
          this.baseUrl + "/product/product",
          {
            productName: this.productName,
            category: {
              id: this.categoryChose,
            },
            description: this.description,
          },
          {
            headers: {
              "Access-Control-Allow-Origin": "*",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          }
        );
        return response.data;
      } catch (error) {
        console.error(error);
      }
    },

    changeCategoryEvent(data) {
      this.categoryChoseTemp = data;
    },

    async btnChangeCategory() {
      this.categoryChose = this.categoryChoseTemp;
      const categoryId = document.getElementById("category-id");
      categoryId.value = (
        await this.getCategoryById(this.categoryChose)
      ).categoryName;
    },

    async loadImageWithHeader() {
      const allImages = document.querySelectorAll("img");
      allImages.forEach((img) => {
        const url = img.src;
        const xhr = new XMLHttpRequest();
        xhr.open("GET", url, true);
        xhr.setRequestHeader("ngrok-skip-browser-warning", "ngrok");

        xhr.responseType = "blob";

        xhr.onload = () => {
          if (xhr.status === 200) {
            const blob = xhr.response;
            const objectURL = URL.createObjectURL(blob);
            img.src = objectURL;
          }
        };

        xhr.send();
      });
    },
  },
  async created() {
    this.getListCategory();
  },
  async mounted() {
    this.loadImageWithHeader();
  },
  computed: {
    tableLength() {
      var result = [];
      var count = 1;
      result.push(count);
      for (var j = this.attributes.length; j > 0; j--) {
        count = count * this.attributes[j - 1].attributeValues.length;
        result.push(count);
      }
      result = result.reverse();
      return result;
    },
  },
};
</script>

<style scoped>
.side-bar {
  height: calc(100vh - 92px);
}

.btn-show-list {
  width: 15px;
  padding: 0;
}

.btn-show-list:hover {
  color: #42b983;
}

.list-group-item {
  border: none;
}

.list-group-item > div > a {
  text-decoration: none;
  color: black;
}

.list-group-item > div > a:hover {
  color: #42b983;
}
@media (max-width: 739px) {
}
</style>
